import React from 'react';
import PropTypes from 'prop-types';
import Link, { navigateTo } from 'gatsby-link';
import Img from 'gatsby-image';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';
import Card, { CardActions, CardContent, CardHeader } from 'material-ui/Card';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';

import TagArray from '../tag-array';

const propTypes = {

};

const styles = theme => ({
  root: {
    '@global': theme.typography.global,
    textAlign: 'left',
    color: theme.palette.secondary.dark,
    margin: '10px 0 0 0',
    transition: `all ${(theme.transitions.duration.standard * 0.001).toFixed(2)}s ${theme.transitions.easing.easeInOut}`,
    borderRadius: 2,
    '&:hover': {
      boxShadow: theme.shadows[20],
      cursor: 'pointer',
    },
    [theme.breakpoints.up('lg')]: {
      maxWidth: 412,
      // margin: theme.spacing.unit,
      margin: '20px',
      // alignContent: 'stretch',
    },
  },

  flexBody: {
    display: 'flex',
    flexWrap: 'wrap',
    alignContent: 'stretch',
    [theme.breakpoints.up('lg')]: {
      justifyContent: 'center',
      // margin: '0 20px',
      // alignContent: 'stretch',
      // flexDirection: 'column',
      flexGrow: 1,
    },
  },
  cover: {
    maxWidth: 640,
    minWidth: 300,
    flexGrow: 2,
    [theme.breakpoints.up('md')]: {
      minWidth: 345,
    },
    [theme.breakpoints.up('lg')]: {
      // minWidth: 345,
    },
  },
  excerpt: {
    flexGrow: 1,
    display: 'flex',
    flexWrap: 'wrap',
    // alignContent: 'stretch',
    // justifyContent: 'center',
    padding: theme.spacing.unit * 2,
    [theme.breakpoints.up('md')]: {
      maxWidth: 400,
    },
    [theme.breakpoints.up('lg')]: {
      // minWidth: 345,
      // hieght: 500,
    },
  },
  title: {
    margin: '0.5em',
    maxWidth: 500,
  },
  contentBody: {
    flexGrow: 2,
    paddingBottom: theme.spacing.unit * 2,
  },
  space: {
    flexGrow: 3,
  },
  tags: {
    flexGrow: 1,
  },
});

const PostExcerpt = (props) => {
  const { classes, cover, excerpt, html, path, title, tags } = props;
  return (
    <Card className={classes.root}>
      <Link to={path}>
        <div className={classes.flexBody}>
          <div className={classes.cover}>
            {cover ? (
              <Img sizes={cover} alt={title} />
            ) : (
              <Typography
                className={classes.title}
                variant="title"
              >
                {title}
              </Typography>
            )}
          </div>
          <div className={classes.excerpt}>
            <div className={classes.contentBody}>
              <Typography variant="body1">{excerpt}</Typography>
            </div>
            {/* <div className={classes.space} /> */}
            <div className={classes.tags}>
              {/* <TagArray tags={tags} /> */}
            </div>
          </div>
        </div>
      </Link>
    </Card>
  );
};

PostExcerpt.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostExcerpt);



